.Treeview {
    width: 100%;
    min-height: 100vh;
    display: flex;
    .Input {
        width: 50%;
        background: #fff;
        padding: 15px;
        margin: 15px;
        box-shadow: 0 0 5px 1px rgba(0,0,0,0.2);
        border-radius: 5px;
        .Error {
            color: red;
            font-weight: bold;
            margin-top: 10px;
        }
    }

    .Tree {
        width: 50%;
        background: #fff;
        padding: 15px;
        margin: 15px;
        margin-left: 0;
        box-shadow: 0 0 5px 1px rgba(0,0,0,0.2);
        border-radius: 5px;
        overflow-x: auto;
    }
}
